import React, { Component } from 'react';
import { Button } from 'react-bootstrap';
import { Modal } from 'react-bootstrap';

class MyVerticallyCenteredModal extends React.Component {
    constructor () {
        super();

        this.state = {
            title: '',
            responsible: '',
            description: '',
            priority: 'low',
            accion:'Sube GastoPublico',
            movilidadcapitales:'Perfecta',
            tipocambio:'Flexible',
            esquema:'Intermedio',
            show: false
          };
        this.handleInputChange = this.handleInputChange.bind(this);      
        this.onClickAceptar = this.onClickAceptar.bind(this);
      }

    onClickAceptar(){
        for (var i = 0; i < this.props.todos.length; i++) {
          if ((this.state.accion == this.props.todos[i].Entrada_Accion) &&
              (this.state.movilidadcapitales == this.props.todos[i].Entrada_MovimientoDeCapitales) &&
              (this.state.tipocambio == this.props.todos[i].Entrada_TipoDeCambio) &&
              (this.state.esquema == this.props.todos[i].Entrada_Esquema)
             ){
            this.props.onAddSeleccionado(this.props.todos[i])
            console.log('Encontró');
          }
        }
    
        this.props.onHide();
    }

    handleInputChange(e) {
        const {value, name} = e.target;
        console.log(value, name);
        this.setState({
          [name]: value
        });
    }
    
  render() {
    return (
      <Modal
        {...this.props}
        size="lg"
        aria-labelledby="contained-modal-title-vcenter"
        centered
      >
        <Modal.Header closeButton>
          <Modal.Title id="contained-modal-title-vcenter">
            Que pasa si ...
          </Modal.Title>
        </Modal.Header>
        <Modal.Body>
            <p>
                Detalla los efectos que tendria si se produce la accion indicada.                
                Con la configuracion de parametros indicados.
            </p>
            <div class="row">
                <div class="col-sm-4" >
                Accion
                </div>
                <div class="col-sm-8" >
                <select
                    name="accion"
                    className="form-control"
                    value={this.state.accion}
                    onChange={this.handleInputChange}
                    >
                    <option>Sube GastoPublico</option>
                    <option>Baja GastoPublico</option>
                    <option>Sube Impuestos</option>
                    <option>Baja Impuestos</option>
                    <option>Sube OfertaMonetaria</option>
                    <option>Baja OfertaMonetaria</option>
                    <option>PaisExtranjero PoneArancel a NuestrasExportaciones</option>
                    <option>PaisExtranjero QuitaArancel a NuestrasExportaciones</option>
                    <option>Sube TasaInteres Internacional</option>
                    <option>Baja TasaInteres Internacional</option>
                    <option>Sube Capital Tecnologia Recursos o Manoobra</option>
                    <option>Baja Capital Tecnologia Recursos o Manoobra</option>
                </select>
                </div>
            </div>
            <div class="row">
                <div class="col-sm-4">
                Esquema
                <select
                    name="esquema"
                    className="form-control"
                    value={this.state.esquema}
                    onChange={this.handleInputChange}
                    >
                    <option>Intermedio</option>
                    <option>Keynesiano</option>
                    <option>Clasico</option>
                </select>
                </div>
                <div class="col-sm-4">
                Tipo Cambio
                <select
                    name="tipocambio"
                    className="form-control"
                    value={this.state.tipocambio}
                    onChange={this.handleInputChange}
                    >
                    <option>Flexible</option>
                    <option>Fijo</option>
                </select>
                </div>
                <div class="col-sm-4">
                Movilidad de Capitales
                <select
                    name="movilidadcapitales"
                    className="form-control"
                    value={this.state.movilidadcapitales}
                    onChange={this.handleInputChange}
                    >
                    <option>Perfecta</option>
                    <option>Alta</option>
                    <option>Baja</option>
                    <option>Nula</option>
                </select>
                </div>
            </div>

        </Modal.Body>
        <Modal.Footer>
          <Button onClick={this.onClickAceptar}>Aceptar</Button>            
          <Button onClick={this.props.onHide}>Close</Button>
        </Modal.Footer>
      </Modal>
    );
  }
}

export default MyVerticallyCenteredModal;