import React, { Component } from 'react';
import arriba from '../Arriba.png';
import abajo from '../Abajo.png';
import igual from '../Igual.png';

var bgColors = { "Default": "#81b71a",
                    "Blue": "#00B1E1",
                    "Cyan": "#37BC9B",
                    "Green": "#8CC152",
                    "Red": "#E9573F",
                    "Yellow": "#F6BB42",
};

const divStyle = {
  backgroundColor: "#ffff42"
};

class DetalleForm extends Component {
  constructor () {
    super();
  }


  textoIndicador(IndicadorNombre,resaltar){
      if (resaltar)
        return (<font style={divStyle}>{IndicadorNombre}</font>)
      else
        return (<font>{IndicadorNombre}</font>)
  }

  render() {
      //console.log(this.props.Detalles);
      if (this.props.Detalles!=undefined){
        const detalles = this.props.Detalles.map((detalle,i)=> {
          var imgAccion
          if (detalle != null){
            if (detalle.valor>0) imgAccion=arriba;
            if (detalle.valor<0) imgAccion=abajo;
            if (detalle.valor==0) imgAccion=igual;
            var resaltar =false;
            if (detalle.IndicadorId==this.props.ResaltarIndicadorId){
              resaltar = true
            }
            console.log(arriba)
            return (
              <div>
              <ul>
                <li>
                  <table >
                    <tbody>
                      <tr>
                        <td><img src={imgAccion}  /></td>
                        <td>{detalle.valor} {this.textoIndicador(detalle.IndicadorNombre,resaltar)}</td>
                      </tr>
                    </tbody>
                  </table>
                  <DetalleForm Mostrar={this.props.Mostrar} Detalles={detalle.Consecuencias} ResaltarIndicadorId={this.props.ResaltarIndicadorId} />
                </li>
              </ul>
              </div>
            )

          }
        })

        if (this.props.Mostrar == true){
          return(
            <div >
              {detalles}
            </div>
          );  
        }else{
          return('r1');  
        }

      }else{
        return('r2');  
      }


  }


}

export default DetalleForm;
