import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
import imgMacroeconomia from './Images/Macroeconomia2.jpg';

import MyVerticallyCenteredModal from './Components/MyVerticallyCenteredModal';
import AreaDeTrabajo from './Components/AreaDeTrabajo';


class App extends Component {
  constructor(){
  	super();
  	this.state = {
  		title: 'Aplicacion de Tareas',
  		ntareas: 10,
      ResaltarIndicadorId: 0,
      verEquilibrioInterno: 'none',
      modalShow: false,
      ComoModalShow:false,
      MostrarDetalle : false,
      iniciarAreaTrabajo : false
  	};
    this.modalClose = this.modalClose.bind(this);
    this.ComoModalClose = this.ComoModalClose.bind(this);
  }
  
  
  clicMostrarDetalle(valor){
    //console.log(xIndicadorID);
    var valorNuevo = !this.state.MostrarDetalle;

    this.setState({
      MostrarDetalle: valorNuevo
    });
  }



  modalClose(valor){
      this.setState({ 
          modalShow: false 
      });
  }

  ComoModalClose(valor){
    this.setState({ 
        ComoModalShow: false 
    });
  }

  
  render() {
    
    console.log('Antes de renderizar el componente')
    if (!this.state.iniciarAreaTrabajo){
      return (
        <div className="App" >
          <br></br><br></br>
          <pre  style={{color: 'white'}}><h2>Macroeconomía</h2></pre>
          <br></br>
          <img src={imgMacroeconomia} /><br></br><br></br> 
          <a style={{color: 'white'}} class="nav-link" href="#" onClick={() => this.setState({ iniciarAreaTrabajo: true })}> Comenzar</a>
        </div>
      )
  }else{
      return (
        <AreaDeTrabajo
        show={this.state.modalShow}
        onHide={this.modalClose}
        todos={this.state.todos} 
        onAddSeleccionado={this.handleAddUnoSeleccionado}                  
        />
      );
    }

  }
}

export default App;
