import React, { Component } from 'react';
import { Button } from 'react-bootstrap';
import { Modal } from 'react-bootstrap';

class ComoModal extends React.Component {
    constructor () {
        super();

        this.state = {
            accionDemandaAgregada:'',
            accionConsumo:'',
            accionTasaInteres:'',
            accionInversion:'',
            accionTipoCambio:'',
            accionExpNetas:'',
            accionReservasBC:'',
            accionOfertaMonetaria:'',   
            accionPrecio:'',
            accionProduccion:'',
            accionDesempleo:'',
            accionDeficitfiscal:'',
            accionDeudapublica:'',
            accionInteresesdeuda :'',        
            movilidadcapitales:'',
            tipocambio:'',
            esquema:'',

            show: false
          };
        this.handleInputChange = this.handleInputChange.bind(this);      
        this.onClickAceptar = this.onClickAceptar.bind(this);
      }

    onClickAceptar(){
        var resultados = this.props.todos.filter((e,i) => {
          // validar si se selecciona Demanda Agregada
          var validoDemandaAgregada = false;
          if (this.state.accionDemandaAgregada=='') validoDemandaAgregada = true;
          if (this.state.accionDemandaAgregada=='Sube'){
              if (e.Salida_EE_DemandaAgregada > 0) validoDemandaAgregada = true;
          }
          if (this.state.accionDemandaAgregada=='Baja'){
            if (e.Salida_EE_DemandaAgregada < 0) validoDemandaAgregada = true;
          }   
          // validar si se selecciona Consumo
          var validoConsumo = false;
          if (this.state.accionConsumo=='') validoConsumo = true;
          if (this.state.accionConsumo=='Sube'){
              if (e.Salida_EE_Consumo > 0) validoConsumo = true;
          }
          if (this.state.accionConsumo=='Baja'){
            if (e.Salida_EE_Consumo < 0) validoConsumo = true;
          }   
          // validar si se selecciona Tasa de interes
          var validoTasaInteres = false;
          if (this.state.accionTasaInteres=='') validoTasaInteres = true;
          if (this.state.accionTasaInteres=='Sube'){
            if (e.Salida_EE_TasaDeInteres > 0) validoTasaInteres = true;
          }
          if (this.state.accionTasaInteres=='Baja'){
            if (e.Salida_EE_TasaDeInteres < 0) validoTasaInteres = true;
          }   
          // validar si se selecciona Inversion
          var validoInversion = false;
          if (this.state.accionInversion=='') validoInversion = true;
          if (this.state.accionInversion=='Sube'){
            if (e.Salida_EE_Inversion > 0) validoInversion = true;
          }
          if (this.state.accionInversion=='Baja'){
            if (e.Salida_EE_Inversion < 0) validoInversion = true;
          }   
          // validar si se selecciona TipoCambio
          var validoTipoCambio = false;
          if (this.state.accionTipoCambio=='') validoTipoCambio = true;
          if (this.state.accionTipoCambio=='Sube'){
            if (e.Salida_EE_TipoDeCambio > 0) validoTipoCambio = true;
          }
          if (this.state.accionInversion=='Baja'){
            if (e.Salida_EE_TipoDeCambio < 0) validoTipoCambio = true;
          }   

          // validar si se selecciona ExpNetas
          var validoExpNetas = false;
          if (this.state.accionExpNetas=='') validoExpNetas = true;
          if (this.state.accionExpNetas=='Sube'){
            if (e.Salida_EE_ExportacionesNetas > 0) validoExpNetas = true;
          }
          if (this.state.accionExpNetas=='Baja'){
            if (e.Salida_EE_ExportacionesNetas < 0) validoExpNetas = true;
          }   

          // validar si se selecciona ReservasBC
          var validoReservasBC = false;
          if (this.state.accionReservasBC=='') validoReservasBC = true;
          if (this.state.accionReservasBC=='Sube'){
            if (e.Salida_BC_Reservas > 0) validoReservasBC = true;
          }
          if (this.state.accionReservasBC=='Baja'){
            if (e.Salida_BC_Reservas < 0) validoReservasBC = true;
          }   

          // validar si se selecciona OfertaMonetaria
          var validoOfertaMonetaria = false;
          if (this.state.accionOfertaMonetaria=='') validoOfertaMonetaria = true;
          if (this.state.accionOfertaMonetaria=='Sube'){
            if (e.Salida_BC_OfertaMonetaria > 0) validoOfertaMonetaria = true;
          }
          if (this.state.accionOfertaMonetaria=='Baja'){
            if (e.Salida_BC_OfertaMonetaria < 0) validoOfertaMonetaria = true;
          }   

          // validar si se selecciona Precio
          var validoPrecio = false;
          if (this.state.accionPrecio=='') validoPrecio = true;
          if (this.state.accionPrecio=='Sube'){
            if (e.Salida_PE_Precio > 0) validoPrecio = true;
          }
          if (this.state.accionPrecio=='Baja'){
            if (e.Salida_PE_Precio < 0) validoPrecio = true;
          }   

          // validar si se selecciona Produccion
          var validoProduccion = false;
          if (this.state.accionProduccion=='') validoProduccion = true;
          if (this.state.accionProduccion=='Sube'){
            if (e.Salida_PE_Produccion > 0) validoProduccion = true;
          }
          if (this.state.accionProduccion=='Baja'){
            if (e.Salida_PE_Produccion < 0) validoProduccion = true;
          }   
          // validar si se selecciona Desempleo
          var validoDesempleo = false;
          if (this.state.accionDesempleo=='') validoDesempleo = true;
          if (this.state.accionDesempleo=='Sube'){
            if (e.Salida_PE_Desempleo > 0) validoDesempleo = true;
          }
          if (this.state.accionDesempleo=='Baja'){
            if (e.Salida_PE_Desempleo < 0) validoDesempleo = true;
          }   

          // validar si se selecciona Deficitfiscal
          var validoDeficitfiscal = false;
          if (this.state.accionDeficitfiscal=='') validoDeficitfiscal = true;
          if (this.state.accionDeficitfiscal=='Sube'){
            if (e.Salida_DeficitFiscal > 0) validoDeficitfiscal = true;
          }
          if (this.state.accionDeficitfiscal=='Baja'){
            if (e.Salida_DeficitFiscal < 0) validoDeficitfiscal = true;
          }   

          // validar si se selecciona Deudapublica
          var validoDeudapublica = false;
          if (this.state.accionDeudapublica=='') validoDeudapublica = true;
          if (this.state.accionDeudapublica=='Sube'){
            if (e.Salida_DeudaPublica > 0) validoDeudapublica = true;
          }
          if (this.state.accionDeudapublica=='Baja'){
            if (e.Salida_DeudaPublica < 0) validoDeudapublica = true;
          }   

          // validar si se selecciona Interesesdeuda
          var validoInteresesdeuda = false;
          if (this.state.accionInteresesdeuda=='') validoInteresesdeuda = true;
          if (this.state.accionInteresesdeuda=='Sube'){
            if (e.Salida_Intereses_DeudaPublica > 0) validoInteresesdeuda = true;
          }
          if (this.state.accionInteresesdeuda=='Baja'){
            if (e.Salida_Intereses_DeudaPublica < 0) validoInteresesdeuda = true;
          }   

          //validar esquema
          var validoEsquema = false;
          if (this.state.esquema ==''){
            validoEsquema = true;
          } 
          if (this.state.esquema != ''){
            if (e.Entrada_Esquema== this.state.esquema){
              validoEsquema = true;
            }
          }

          //validar configuracion tipo de cambio
          var validoConfTipoCambio = false;
          if (this.state.tipocambio  ==''){
            validoConfTipoCambio = true;
          } 
          if (this.state.tipocambio != ''){
            if (e.Entrada_TipoDeCambio== this.state.tipocambio){
              validoConfTipoCambio = true;
            }
          }


          //validar movilidad de capitales
          var validoMovilidadDeCapitales = false;
          if (this.state.movilidadcapitales ==''){
            validoMovilidadDeCapitales = true;
          } 
          if (this.state.movilidadcapitales != ''){
            if (e.Entrada_MovimientoDeCapitales== this.state.movilidadcapitales){
              validoMovilidadDeCapitales = true;
            }
          }

          return (validoDemandaAgregada && validoConsumo && validoTasaInteres && validoInversion && validoTipoCambio && validoExpNetas && validoReservasBC && validoOfertaMonetaria && validoPrecio && validoProduccion && validoDesempleo && validoDeficitfiscal && validoDeudapublica && validoInteresesdeuda && validoEsquema && validoConfTipoCambio && validoMovilidadDeCapitales);
        })
        // for (var i = 0; i < this.props.todos.length; i++) {
        //   var valido = false;
        //   if (this.state.accionDemandaAgregada == 'Sube'){
        //       if (this.props.todos[i].Salida_EE_DemandaAgregada > 0){
        //             valido = true;
        //       }
        //   }
        //   if (valido){
        //     this.props.onAddSeleccionado(this.props.todos[i])
        //     console.log('Encontro ' + i + ' '  + this.props.todos[i].Entrada_Accion );
        //   }
        // }

        //devolver resultados a la funcion que se pasó como parametro
        this.props.onAddSeleccionado(resultados)

        //Cerrar form modal
        this.props.onHide();
    }

    handleInputChange(e) {
        const {value, name} = e.target;
        console.log(value, name);
        this.setState({
          [name]: value
        });
    }
    
  render() {
    return (
      <Modal
        {...this.props}
        size="lg"
        aria-labelledby="contained-modal-title-vcenter"
        centered
      >
        <Modal.Header closeButton>
          <Modal.Title id="contained-modal-title-vcenter">
            Como ...
          </Modal.Title>
        </Modal.Header>
        <Modal.Body>
            <p>
                Detalla las acciones o situaciones que lleva a producir la accion seleccionada.
                Con la configuracion de parametros indicados.
            </p>
            <div class="row">
                <div class="col-sm-4" >
                Demanda Agregada
                </div>
                <div class="col-sm-2" >
                <select
                    name="accionDemandaAgregada"
                    className="form-control"
                    value={this.state.accionDemandaAgregada}
                    onChange={this.handleInputChange}
                    >
                    <option></option>
                    <option>Sube</option>
                    <option>Baja</option>
                </select>
                </div>
                <div class="col-sm-4" >
                Consumo
                </div>
                <div class="col-sm-2" >
                <select
                    name="accionConsumo"
                    className="form-control"
                    value={this.state.accionConsumo}
                    onChange={this.handleInputChange}
                    >
                    <option></option>
                    <option>Sube</option>
                    <option>Baja</option>
                </select>
                </div>
            </div>
            <div class="row">
                <div class="col-sm-4" >
                Tasa de interes
                </div>
                <div class="col-sm-2" >
                <select
                    name="accionTasaInteres"
                    className="form-control"
                    value={this.state.accionTasaInteres}
                    onChange={this.handleInputChange}
                    >
                    <option></option>
                    <option>Sube</option>
                    <option>Baja</option>
                </select>
                </div>
                <div class="col-sm-4" >
                Inversion
                </div>
                <div class="col-sm-2" >
                <select
                    name="accionInversion"
                    className="form-control"
                    value={this.state.accionInversion}
                    onChange={this.handleInputChange}
                    >
                    <option></option>
                    <option>Sube</option>
                    <option>Baja</option>
                </select>
                </div>
            </div>
            <div class="row">
                <div class="col-sm-4" >
                  Tipo de cambio
                </div>
                <div class="col-sm-2" >
                  <select
                      name="accionTipoCambio"
                      className="form-control"
                      value={this.state.accionTipoCambio}
                      onChange={this.handleInputChange}
                      >
                      <option></option>
                      <option>Sube</option>
                      <option>Baja</option>
                  </select>
                </div>
                <div class="col-sm-4" >
                  Exportaciones Netas
                </div>
                <div class="col-sm-2" >
                  <select
                      name="accionExpNetas"
                      className="form-control"
                      value={this.state.accionExpNetas}
                      onChange={this.handleInputChange}
                      >
                      <option></option>
                      <option>Sube</option>
                      <option>Baja</option>
                  </select>
                </div>
            </div>
            <div class="row">
                <div class="col-sm-4" >
                  Reservas BC
                </div>
                <div class="col-sm-2" >
                  <select
                      name="accionReservasBC"
                      className="form-control"
                      value={this.state.accionReservasBC}
                      onChange={this.handleInputChange}
                      >
                      <option></option>
                      <option>Sube</option>
                      <option>Baja</option>
                  </select>
                </div>
                <div class="col-sm-4" >
                  Oferta Monetaria
                </div>
                <div class="col-sm-2" >
                  <select
                      name="accionOfertaMonetaria"
                      className="form-control"
                      value={this.state.accionOfertaMonetaria}
                      onChange={this.handleInputChange}
                      >
                      <option></option>
                      <option>Sube</option>
                      <option>Baja</option>
                  </select>
                </div>
            </div>
            <div class="row">
                <div class="col-sm-2" >
                  Precio
                </div>
                <div class="col-sm-2" >
                  <select
                      name="accionPrecio"
                      className="form-control"
                      value={this.state.accionPrecio}
                      onChange={this.handleInputChange}
                      >
                      <option></option>
                      <option>Sube</option>
                      <option>Baja</option>
                  </select>
                </div>
                <div class="col-sm-2" >
                  Produccion
                </div>
                <div class="col-sm-2" >
                  <select
                      name="accionProduccion"
                      className="form-control"
                      value={this.state.accionProduccion}
                      onChange={this.handleInputChange}
                      >
                      <option></option>
                      <option>Sube</option>
                      <option>Baja</option>
                  </select>
                </div>
                <div class="col-sm-2" >
                  Desempleo
                </div>
                <div class="col-sm-2" >
                  <select
                      name="accionDesempleo"
                      className="form-control"
                      value={this.state.accionDesempleo}
                      onChange={this.handleInputChange}
                      >
                      <option></option>
                      <option>Sube</option>
                      <option>Baja</option>
                  </select>
                </div>
            </div>

            <div class="row">
                <div class="col-sm-2" >
                  Deficit fiscal
                </div>
                <div class="col-sm-2" >
                  <select
                      name="accionDeficitfiscal"
                      className="form-control"
                      value={this.state.accionDeficitfiscal}
                      onChange={this.handleInputChange}
                      >
                      <option></option>
                      <option>Sube</option>
                      <option>Baja</option>
                  </select>
                </div>
                <div class="col-sm-2" >
                  Deuda Publica
                </div>
                <div class="col-sm-2" >
                  <select
                      name="accionDeudapublica"
                      className="form-control"
                      value={this.state.accionDeudapublica}
                      onChange={this.handleInputChange}
                      >
                      <option></option>
                      <option>Sube</option>
                      <option>Baja</option>
                  </select>
                </div>
                <div class="col-sm-2" >
                  Intereses Deuda
                </div>
                <div class="col-sm-2" >
                  <select
                      name="accionInteresesdeuda"
                      className="form-control"
                      value={this.state.accionInteresesdeuda}
                      onChange={this.handleInputChange}
                      >
                      <option></option>
                      <option>Sube</option>
                      <option>Baja</option>
                  </select>
                </div>
            </div>

            <div class="row">
                <div class="col-sm-4">
                Esquema
                <select
                    name="esquema"
                    className="form-control"
                    value={this.state.esquema}
                    onChange={this.handleInputChange}
                    >
                    <option></option>
                    <option>Intermedio</option>
                    <option>Keynesiano</option>
                    <option>Clasico</option>
                </select>
                </div>
                <div class="col-sm-4">
                Tipo Cambio
                <select
                    name="tipocambio"
                    className="form-control"
                    value={this.state.tipocambio}
                    onChange={this.handleInputChange}
                    >
                    <option></option>
                    <option>Flexible</option>
                    <option>Fijo</option>
                </select>
                </div>
                <div class="col-sm-4">
                Movilidad de Capitales
                <select
                    name="movilidadcapitales"
                    className="form-control"
                    value={this.state.movilidadcapitales}
                    onChange={this.handleInputChange}                    
                    >
                    <option></option>
                    <option>Nula</option>
                    <option>Baja</option>
                    <option>Alta</option>
                    <option>Perfecta</option>
                </select>
                </div>
            </div>

        </Modal.Body>
        <Modal.Footer>
          <Button onClick={this.onClickAceptar}>Aceptar</Button>            
          <Button onClick={this.props.onHide}>Close</Button>
        </Modal.Footer>
      </Modal>
    );
  }
}

export default ComoModal;