import React, { Component } from 'react';
class TodoForm extends Component {
  constructor () {
    super();

    this.state = {
      title: '',
      responsible: '',
      description: '',
      priority: 'low',
      accion:'',
      movilidadcapitales:'',
      tipocambio:'',
      esquema:'',
      show: false
    };
    this.handleInputChange = this.handleInputChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleSubmit(e) {
    e.preventDefault();//evita refrescar la pagina
    //console.log(this.props.todos);
    for (var i = 0; i < this.props.todos.length; i++) {
      // console.log(this.state.accion);
      // console.log(this.props.todos[i].Entrada_Accion);
      // console.log('-----------');
      // console.log(this.state.movilidadcapitales);
      // console.log(this.props.todos[i].Entrada_MovimientoDeCapitales);
      // console.log('-----------');
      // console.log(this.state.esquema);
      // console.log(this.props.todos[i].Entrada_Esquema);
      // console.log('========================================');
      if ((this.state.accion == this.props.todos[i].Entrada_Accion) &&
          (this.state.movilidadcapitales == this.props.todos[i].Entrada_MovimientoDeCapitales) &&
          (this.state.tipocambio == this.props.todos[i].Entrada_TipoDeCambio) &&
          (this.state.esquema == this.props.todos[i].Entrada_Esquema)
         ){
        this.props.onAddSeleccionado(this.props.todos[i])
        console.log('Encontró');
      }
    }
    //this.props.onAddTodo(this.state);
    /*this.setState({
      title: '',
      responsible: '',
      description: '',
      priority: 'low'
    });*/
  }

  handleInputChange(e) {
    const {value, name} = e.target;
    console.log(value, name);
    this.setState({
      [name]: value
    });
  }


  render() {
    return (

      

          <div   class="modal-dialog modal-lg" role="document">
            <div class="modal-content">
              <div class="modal-header">
                <h5 class="modal-title" id="exampleModalLabel">Modal title</h5>
                <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                  <span aria-hidden="true">&times;</span>
                </button>
              </div>
              <div class="modal-body">
                <div class="row">
                  <div class="col-sm-4" >
                    Accion
                  </div>
                  <div class="col-sm-8" >
                    <select
                        name="accion"
                        className="form-control"
                        value={this.state.accion}
                        onChange={this.handleInputChange}
                      >
                      <option>Sube_GastoPublico</option>
                      <option>Baja_GastoPublico</option>
                      <option>Sube_OfertaMonetaria</option>
                      <option>Baja_OfertaMonetaria</option>
                      <option>PaisExtranjero_PoneArancel_aNuestrasExportaciones</option>
                      <option>Sube_TasaDeInteresInternacional</option>
                      <option>Baja_TasaDeInteresInternacional</option>
                    </select>
                  </div>
                </div>
                <div class="row">
                  <div class="col-sm-4">
                    Movilidad de Capitales
                    <select
                        name="movilidadcapitales"
                        className="form-control"
                        value={this.state.movilidadcapitales}
                        onChange={this.handleInputChange}
                      >
                      <option>Nula</option>
                      <option>Baja</option>
                      <option>Alta</option>
                      <option>Perfecta</option>
                    </select>
                  </div>
                  <div class="col-sm-4">
                    Tipo Cambio
                    <select
                        name="tipocambio"
                        className="form-control"
                        value={this.state.tipocambio}
                        onChange={this.handleInputChange}
                      >
                      <option>Flexible</option>
                      <option>Fijo</option>
                    </select>
                  </div>
                  <div class="col-sm-4">
                  </div>
                  <div class="col-sm-4">
                    Esquema
                    <select
                        name="esquema"
                        className="form-control"
                        value={this.state.esquema}
                        onChange={this.handleInputChange}
                      >
                      <option>Intermedio</option>
                      <option>Keynesiano</option>
                      <option>Clasico</option>
                    </select>
                  </div>
                </div>
              </div>
              <div class="modal-footer">
                <button type="submit" className="btn btn-primary">Aceptar</button>
                <button type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
                <button type="button" class="btn btn-primary">Save changes</button>
              </div>
            </div>
          </div>



    );
  }

}

export default TodoForm;
