
import React, { Component } from 'react';
import logo from '../logo.svg';
//import './App.css';
import imgArriba from '../Arriba.png';
import imgAbajo from '../Abajo.png';
import imgIgual from '../Igual.png';
import imgMacroeconomia from '../Images/Mundo.png';

import {todos}  from '../Todos.json';
import {seleccionados}  from '../Seleccionados.json';

import TodoForm from './TodoForm';
import DetalleForm from './DetalleForm';


//console.log(todos);

import { Button, Table } from 'react-bootstrap';
import { Form } from 'react-bootstrap';
import MyVerticallyCenteredModal from './MyVerticallyCenteredModal';
import ComoModal from './ComoModal';



class AreaDeTrabajo extends Component {
  constructor(){
  	super();
  	this.state = {
  		title: 'Aplicacion de Tareas',
  		ntareas: 10,
      ResaltarIndicadorId: 0,
      verEquilibrioInterno: 'none',
      todos,
      seleccionados,
      modalShow: false,
      ComoModalShow:false,
      MostrarDetalle : false
  	};
    this.handleAddTodo =  this.handleAddTodo.bind(this);
    this.handleAddUnoSeleccionado = this.handleAddUnoSeleccionado.bind(this);
    this.handleAddSeleccionado =  this.handleAddSeleccionado.bind(this);
    this.modalClose = this.modalClose.bind(this);
    this.ComoModalClose = this.ComoModalClose.bind(this);
  }
  handleAddTodo(todo){
  	this.setState({
  		todos: [...this.state.todos,todo]
  	})
  }

  handleAddUnoSeleccionado(seleccionado){
    //seleccionado contiene un objetos seleccionado
    this.setState({
      seleccionados: [...this.state.seleccionados,seleccionado]
  	})
  }

  handleAddSeleccionado(seleccionado){
    //seleccionado contiene un array de objetos seleccionados
    this.setState({
      seleccionados: [...this.state.seleccionados,...seleccionado]
  	})
  }
  
  removeTodo(index){
    //console.log(index);
    this.setState({
      seleccionados: this.state.seleccionados.filter((e,i) => {
        return i !== index
      })
    })
  }

  AccionResaltarIndicador(xIndicadorID){
    //console.log(xIndicadorID);
    this.setState({
      ResaltarIndicadorId: xIndicadorID
    });
  }

  clicMostrarDetalle(valor){
    //console.log(xIndicadorID);
    var valorNuevo = !this.state.MostrarDetalle;

    this.setState({
      MostrarDetalle: valorNuevo
    });
  }


  // MostrarDetalle(ObjDetalle){
  //   return (<pre align="left">Prueba</pre>);
  // }

  ImagenAccion(valor){
      //console.log('ImagenAccion')
      //console.log(valor)
      if (valor>0){
        return (imgArriba)
      }
      if (valor<0){
        return (imgAbajo)
      }
      if (valor==0){
        return (imgIgual)
      }
  }

  modalClose(valor){
      this.setState({ 
          modalShow: false 
      });
  }

  ComoModalClose(valor){
    this.setState({ 
        ComoModalShow: false 
    });
  }

  EscribirIndicador(IndicadorId, IndicadorNombre,valor){
      if(valor==0) valor='';
      return (<font size="3">
                <button className="btn btn-link"  
                  onClick = {this.AccionResaltarIndicador.bind(this,IndicadorId)} >
                  {IndicadorNombre}
                </button>
                <img src={this.ImagenAccion(valor)}/>
                  {valor}
                  </font>);                  
  }

  EscribirCabezalLista(){
    if (this.state.seleccionados.length>0){
      return (
        <tr>
          <td><b>Accion</b></td>
          <td><b>Esquema</b></td>
          <td><b>Tc.Fijo</b></td>
          <td><b>Tc.Flexible</b></td>
        </tr>
      );                  
    }else{
      return ('');
    }

  }

  // rawMarkup(valor){
  //   return (<a onClick={this.removeTodo.bind(this,1)}>{valor}</a>);
  // }

  rawMarkup(valor){
    // return { __html: {valor} };    
    //return (<b> {valor} </b> );
    return (<button className="btn btn-link" onClick = "" > {valor} </button>  );
  }

  // drawHpBar=function(hp){
  //   let hpBars = "";
  //   for(let i=0;i<hp;i++){
  //     hpBars = hpBars + '<div className="hpTick" id='+'"'+i+'"'+'></div>'
  //   }
  //   return hpBars;
  // } 

  render() {
    //let modalClose = () => this.setState({ modalShow: false });

    console.log('Antes de renderizar el componente')

    //armar array de resultados
    var arrayres=[];
    var AntAccion = '';
    var AntEsquema = '';
    var linres = undefined;
    for(var i=0;i<this.state.seleccionados.length;i++){
      var linea = this.state.seleccionados[i];
      //linea.Entrada_Accion
      //linea.Entrada_Esquema
      //linea.Entrada_TipoDeCambio
      //linea.Entrada_MovimientoDeCapitales

      if (AntAccion != linea.Entrada_Accion || AntEsquema != linea.Entrada_Esquema ){
        linres = new Object();
        linres.Accion = linea.Entrada_Accion;
        linres.Esquema = linea.Entrada_Esquema;
        linres.TcFijo = '';
        linres.TcFlexible = '';
        if (linres != undefined) arrayres.push(linres);
        AntAccion = linea.Entrada_Accion;
        AntEsquema = linea.Entrada_Esquema;
      }

      if (linea.Entrada_TipoDeCambio == 'Fijo'){
        linres.TcFijo = linres.TcFijo +  linea.Entrada_MovimientoDeCapitales + ' ' ; //.substring(0, 1);
      }
      if (linea.Entrada_TipoDeCambio == 'Flexible'){
        linres.TcFlexible  = linres.TcFlexible +  linea.Entrada_MovimientoDeCapitales + ' '; //.substring(0, 1) 
      }

    }

    const seleccionadosRes = arrayres.map((todo,i)=> {
    	return (
        <tr>
          <td>
            {todo.Accion}
          </td>
          <td>
            {todo.Esquema}
          </td>
          <td>
            {todo.TcFijo}
          </td>
          <td>
            {/* {todo.TcFlexible} */}
            {this.rawMarkup(todo.TcFlexible)}
          </td>
        </tr>
      )
    })

    const seleccionadosCard = this.state.seleccionados.map((todo,i)=> {
      if (this.state.MostrarDetalle == true){
        return (
          <div className="col-md-4" key={i}>
            <div className="card mt-4">
              <div className="card-header">
                <h4>{todo.Entrada_Accion}</h4>
                <h6>Esquema: {todo.Entrada_Esquema}</h6>
                <h6>Tipo de Cambio: {todo.Entrada_TipoDeCambio}</h6>
                <h6>Movimiento de Capitales: {todo.Entrada_MovimientoDeCapitales}</h6>
                <span className="badge badge-pill badge-danger ml-2">
                  {todo.priority}
                </span>
              </div>
              <div className="card-body">  
                <pre align="left"><DetalleForm Mostrar={this.state.MostrarDetalle} Detalles={todo.DetalleEI} ResaltarIndicadorId={this.state.ResaltarIndicadorId} /></pre>
                {this.EscribirIndicador(2, 'Demanda Agregada',todo.Salida_EI_DemandaAgregada)}<br></br>
                {this.EscribirIndicador(10, 'Tasa de interes',todo.Salida_EI_TasaDeInteres)}<br></br>
                <pre align="left"><DetalleForm Mostrar={this.state.MostrarDetalle} Detalles={todo.DetalleBP} ResaltarIndicadorId={this.state.ResaltarIndicadorId} /></pre>
                {this.EscribirIndicador(23, 'Balanza De Pagos',todo.Salida_BP_BalanzaDePagos)}
                <pre align="left"><DetalleForm Mostrar={this.state.MostrarDetalle} Detalles={todo.DetalleEE} ResaltarIndicadorId={this.state.ResaltarIndicadorId} /></pre>
                {this.EscribirIndicador(2, 'Demanda Agregada',todo.Salida_EE_DemandaAgregada)}<br></br>
                {this.EscribirIndicador(38, 'Consumo',todo.Salida_EE_Consumo)}<br></br><br></br>
                {this.EscribirIndicador(10, 'Tasa de interes',todo.Salida_EE_TasaDeInteres)}<br></br>
                {this.EscribirIndicador(11, 'Inversion',todo.Salida_EE_Inversion)}<br></br><br></br>
                {this.EscribirIndicador(17, 'Tipo de Cambio',todo.Salida_EE_TipoDeCambio)}<br></br>
                {this.EscribirIndicador(25, 'Exp. Netas',todo.Salida_EE_ExportacionesNetas)}<br></br><br></br>
                {this.EscribirIndicador(20, 'Reservas BC',todo.Salida_BC_Reservas)}<br></br>
                {this.EscribirIndicador(12, 'Oferta Monetaria',todo.Salida_BC_OfertaMonetaria)}<br></br><br></br>
  
                <pre align="left"><DetalleForm Mostrar={this.state.MostrarDetalle} Detalles={todo.DetallePE} ResaltarIndicadorId={this.state.ResaltarIndicadorId} /></pre>
                {this.EscribirIndicador(27, 'Precio',todo.Salida_PE_Precio)}<br></br>
                {this.EscribirIndicador(30, 'Produccion',todo.Salida_PE_Produccion)}<br></br>
                {this.EscribirIndicador(29, 'Desempleo',todo.Salida_PE_Desempleo)}<br></br><br></br>

                {this.EscribirIndicador(35, 'Déficit fiscal',todo.Salida_DeficitFiscal)}<br></br>
                {this.EscribirIndicador(36, 'Deuda Pública',todo.Salida_DeudaPublica)}<br></br>
                {this.EscribirIndicador(37, 'Intereses Deuda',todo.Salida_Intereses_DeudaPublica)}<br></br>
  
              </div>
              <div className="card-footer">
                <button
                  className="btn btn-danger"
                  onClick = {this.removeTodo.bind(this,i)}
                >
                  Cerrar
                </button>
              </div>
            </div>
          </div>
        )
  
      }else{
        return (
          <div className="col-md-4" key={i}>
            <div className="card mt-4">
              <div className="card-header">
                <h4>{todo.Entrada_Accion}</h4>
                <h6>Esquema: {todo.Entrada_Esquema}</h6>
                <h6>Tipo de Cambio: {todo.Entrada_TipoDeCambio}</h6>
                <h6>Movimiento de Capitales: {todo.Entrada_MovimientoDeCapitales}</h6>
                <span className="badge badge-pill badge-danger ml-2">
                  {todo.priority}
                </span>
              </div>
              <div className="card-body">  
                {this.EscribirIndicador(2, 'Demanda Agregada',todo.Salida_EE_DemandaAgregada)}<br></br>
                {this.EscribirIndicador(38, 'Consumo',todo.Salida_EE_Consumo)}<br></br><br></br>
                {this.EscribirIndicador(10, 'Tasa de interes',todo.Salida_EE_TasaDeInteres)}<br></br>
                {this.EscribirIndicador(11, 'Inversion',todo.Salida_EE_Inversion)}<br></br><br></br>
                {this.EscribirIndicador(17, 'Tipo de Cambio',todo.Salida_EE_TipoDeCambio)}<br></br>
                {this.EscribirIndicador(25, 'Exp. Netas',todo.Salida_EE_ExportacionesNetas)}<br></br><br></br>
                {this.EscribirIndicador(20, 'Reservas BC',todo.Salida_BC_Reservas)}<br></br>
                {this.EscribirIndicador(12, 'Oferta Monetaria',todo.Salida_BC_OfertaMonetaria)}<br></br><br></br>
  
                {this.EscribirIndicador(27, 'Precio',todo.Salida_PE_Precio)}<br></br>
                {this.EscribirIndicador(30, 'Produccion',todo.Salida_PE_Produccion)}<br></br>
                {this.EscribirIndicador(29, 'Desempleo',todo.Salida_PE_Desempleo)}<br></br><br></br>

                {this.EscribirIndicador(35, 'Déficit fiscal',todo.Salida_DeficitFiscal)}<br></br>
                {this.EscribirIndicador(36, 'Deuda Pública',todo.Salida_DeudaPublica)}<br></br>
                {this.EscribirIndicador(37, 'Intereses Deuda',todo.Salida_Intereses_DeudaPublica)}<br></br>
              </div>
              <div className="card-footer">
                <button
                  className="btn btn-danger"
                  onClick = {this.removeTodo.bind(this,i)}
                >
                  Cerrar
                </button>
              </div>
            </div>
          </div>
        )  
      }
    })

    return (      
      <div className="AreaDeTrabajo">
 
        <nav className="navbar navbar-expand-lg navbar-dark bg-dark fixed-top">
           
          <a class="navbar-brand" href="#"><img src={imgMacroeconomia} /> Macroeconomía</a>          

          <a href="#" className="text-white">
            <span className="badge badge-pill badge-light ml-2">
              {this.state.seleccionados.length}
            </span>
          </a>

          <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarTogglerDemo02" aria-controls="navbarTogglerDemo02" aria-expanded="false" aria-label="Toggle navigation">
            <span class="navbar-toggler-icon"></span>
          </button>


          <div class="collapse navbar-collapse" id="navbarTogglerDemo02">
            <ul class="navbar-nav mr-auto mt-2 mt-lg-0">
              <li class="nav-item active">
              </li>
              <li class="nav-item">
                <MyVerticallyCenteredModal
                    show={this.state.modalShow}
                    onHide={this.modalClose}
                    todos={this.state.todos} 
                    onAddSeleccionado={this.handleAddUnoSeleccionado}                  
                  />
                <a class="nav-link" href="#" onClick={() => this.setState({ modalShow: true })}>Que pasa si ... </a>
              </li>              
              <li class="nav-item">
                <ComoModal
                    show={this.state.ComoModalShow}
                    onHide={this.ComoModalClose}
                    todos={this.state.todos} 
                    onAddSeleccionado={this.handleAddSeleccionado}                  
                  />
                <a class="nav-link" href="#" onClick={() => this.setState({ ComoModalShow: true })}>Como ... </a>
              </li>
              <li class="nav-item">
                <a class="nav-link" href="#" onClick={() => this.setState({ seleccionados: [] })}>Quitar todos </a>
              </li>
            </ul>
            <Form inline>
            {['checkbox'].map(type => (
              <div key={`inline-${type}`} >
                <Form.Check inline label="Detalles" type={type} id={`inline-${type}-1`}  className="text-white" onClick = {this.clicMostrarDetalle.bind(this,true)}   />
              </div>
            ))}
            </Form>          
          </div>


        </nav>
          <br></br><br></br><br></br>
          <Table striped bordered hover variant="dark" >
                            {this.EscribirCabezalLista()}
                            {/* <tr>
                              <td><b>Accion</b></td>
                              <td><b>Esquema</b></td>
                              <td><b>Tc.Fijo</b></td>
                              <td><b>Tc.Flexible</b></td>
                            </tr> */}
                            {seleccionadosRes}
          </Table>

          <div className="row mt-1 ml-1 mr-1">
            <div className="col md-1" colSpan="3">
              <div className="row">
                {seleccionadosCard}
              </div>
            </div>
          </div>



      </div>
    );
  }
}

export default AreaDeTrabajo;
